
import { defineComponent, computed, ref, reactive } from 'vue'
import { role } from '../config/data'
import { useStore } from 'vuex'
import { ElMessage, ElMessageBox } from 'element-plus'
export default defineComponent({
  name: '',
  setup(props, ctx) {
    const chooseTab = ref('first')
    const store = useStore()
    const passwords = reactive({
      confirmPassword: '',
      idUser: '',
      newPassword: '',
      password: ''
    })
    const user = computed(() => store.state.userModule.user)
    const editUserPassword = () => {
      store.dispatch('userModule/updateUserPassword', passwords)
      ctx.emit('closeDialog')
    }
    const editUserInfo = () => {
      ElMessageBox.confirm('是否确定修改用户信息?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      })
        .then(() => {
          store.dispatch('userModule/updateUserInfo')
          ctx.emit('closeDialog')
        })
        .catch(() => {
          ElMessage({
            type: 'info',
            message: '已取消修改'
          })
        })
    }
    const response = {
      xs: 20,
      sm: 20,
      md: 20,
      lg: 20,
      xl: 20
    }
    return {
      user,
      chooseTab,
      role,
      response,
      editUserInfo,
      passwords,
      editUserPassword
    }
  }
})
